.navbar-inverse .navbar-brand {
  margin-left: 50px !important;
  margin-top: 10px; }

.navbar-inverse .navbar-right {
  margin-right: 50px !important; }

.navbar-inverse .navbar-nav > li > a {
  display: block;
  text-decoration: none;
  color: #FFFFFF !important; }

.navbar-inverse .open a {
  background-color: #009688 !important; }
  .navbar-inverse .open a:focus {
    background-color: #009688 !important; }
  .navbar-inverse .open a:hover {
    background-color: #009688 !important; }

.navbar-inverse .dropdown-menu {
  background-color: #424958;
  padding: 10px;
  top: 40px; }
  .navbar-inverse .dropdown-menu li a {
    background-color: #424958 !important;
    color: #FFFFFF; }
    .navbar-inverse .dropdown-menu li a:hover {
      background-color: #51c1b1 !important; }
  .navbar-inverse .dropdown-menu li i {
    min-width: 20px; }

.navbar-inverse .current-project-name {
  font-size: 13px;
  color: #51c1b1;
  top: 5px;
  position: relative; }

.navbar-inverse .invitations {
  width: 310px;
  min-width: 0;
  background-color: #424958 !important;
  color: #FFFFFF; }
  .navbar-inverse .invitations .dropdown-menu li {
    display: block; }
  .navbar-inverse .invitations .accept-decline {
    position: relative;
    top: -10px; }
    .navbar-inverse .invitations .accept-decline span:first-of-type {
      margin-right: 10px; }
    .navbar-inverse .invitations .accept-decline span {
      cursor: pointer;
      font-size: 25px; }

.navbar-inverse .count-info {
  line-height: 12px;
  padding: 2px 6px;
  position: absolute;
  right: 6px;
  top: 8px;
  color: #1ab394;
  background-color: #FFFFFF;
  font-family: 'Open Sans';
  font-size: 10px;
  font-weight: 600;
  text-shadow: none;
  border-radius: 5px; }

@media (min-width: 768px) {
  .navbar-inverse .navbar-top-links .invitations, .navbar-inverse .navbar-top-links .dropdown-tasks, .navbar-inverse .navbar-top-links .dropdown-alerts {
    margin-left: auto; } }
